* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

a {
    text-decoration: none;
    color: #1e3b9e;
}

a.branding {
    color: #ffffff;
}

#root {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 100vh;
}

/* content section */

.container {
    width: 70%;
    margin: auto;
}

#address404 {
    color: red;
}

.loader {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
    min-height: 100vh;
}

.book-link {
    padding: 10px;
    border-radius: 5px;
    font-weight: bold;
    background: #f5f5f6;
    display: block;
    width: 100%;
}

footer {
    text-align: center;
    background: #f5f5f5;
    margin-top: 30px;
}
